:root {
  --text-color: #000;
  --background-color: rgb(212, 221, 228);
  --footerbg: #000;
  --footercolor: #fff;
}
body {
  font-family: "Open Sans", sans-serif;
  padding: 0;
  margin: 0;
  color: var(--text-color);
  background-color: var(--background-color);
}
img {
  max-width: 100%;
}
#app {
  position: relative;
  min-height: 100vh;
  padding-bottom: 60px;
}
nav.navbar {
  box-shadow: 0px 0px 0px 2px red;
  margin-bottom: 20px;
}
.logo_container {
  display: flex;
}
.logo_container a {
  color: var(--text-color);
}
.logo_container .fa {
  font-size: 22px;
  margin-right: 10px;
}
.main_menu {
  display: flex;
  padding: 0;
  margin: 0;
}
.main_menu li {
  list-style: none;
}
.main_menu li a {
  padding: 15px;
  font-size: 16px;
  color: var(--text-color);
}
footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 0;
  background: var(--footerbg);
  color: var(--footercolor);
}
.center-btn {
  margin: 0 auto;
  display: flex;
}

.user_container {
  margin-top: 15px;
}
.user_item_container {
  margin-bottom: 10px;
}
.user_container div.user_item {
  background: #fff;
  padding: 10px;
  text-align: center;
  height: 100%;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
}
.user_container .user_login {
  margin-top: 10px;
  word-break: break-all;
}
a.user_detail_link {
  display: block;
  background: #000;
  color: #fff;
  border-radius: 7px;
  padding: 5px 0px;
  text-transform: capitalize;
}
a.user_detail_link:hover {
  background: var(--background-color);
  transition: all 0.3s linear;
  color: #000;
  text-decoration: none;
  transform: scale3d(1.05, 1.05, 1.05);
}
button:focus {
  box-shadow: none;
}
.buttons_container {
  text-align: center;
}
.resetbtn {
  margin-left: 10px;
}
.userdetail_bio {
  color: brown;
  margin: 10px 0px;
  font-size: 14px;
}
.repos_container {
  margin-top: 30px;
}
.repo_detail_container {
  padding: 5px;
  border: 1px solid #c0c0c0;
  margin: 5px 0;
}
